.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.mainContainer {
  padding: 0%;
}

.contentContainer {
  display: flex;
  background-color: #f4f4f4;
  position: absolute;
  width: 100%;
  height: 90%;
  /* background-image: url("./images/background.jpg"); */
}

.inputContainer {
  width: 60% !important;
}

.contentContainer .container {
  margin-top: 15px;
}

.contentContainer .btn-secondary {
  margin-left: 10px;
}

.btn .spinner-border {
  width: 1rem;
  height: 1rem;
  margin-right: 5px;
}

.card {
  margin-bottom: 15px;
  box-shadow: 2px 2px #e3e3e3;
  padding: 5px 0;
}

.card.active {
  box-shadow: 2px 2px #0c6dfd;
}

.card .card-body {
  display: flex;
  padding: 5px 1rem;
}

.card-body b {
  width: 90px;
}

.card-body .prompt {
  flex: 1 1 0;
  background-color: #eeeeee;
  font-style: italic;
  font-weight: bold;
}

.card-body .response {
  flex: 1 1 0;
  font-style: italic;
  text-align: justify;
}

.responses {
  position: relative;
  height: 95%;
  overflow-y: auto;
}

.toast-body {
  width: 300px;
}